export default {
	data() {
		return {
			InputBottom: 0
		};
	},
	methods: {
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		}
	},
	onLoad() {
		uni.connectSocket({
			url: 'ws://sl.daikan8.com:8282',
		});
		uni.onSocketOpen(function(res) {
			uni.sendSocketMessage({
				data: 'Hello World'
			});
		});
		uni.onSocketMessage(function(res) {
			console.log(res);
		});
	}
}
